import React from 'react';
import SidebarLink from './SidebarLink';
import {Speedometer2, MusicPlayer, Bag,  Gear, MusicNoteBeamed, HandThumbsUp} from 'react-bootstrap-icons';
import './SidebarLinks.css'


const SidebarLinks = ({links}) => {
    return (
        <ul className="SidebarLinks">
            {links.map((link, idx) => (
                    <SidebarLink  title={link.title} icon={link.icon} key={`${link.title}${idx}`}/>
            ))}
        </ul>
    )
}

const links = [
    {
        id: "dashboard",
        title:"Summary",
        icon:<Speedometer2 />
    },
    {   
        id: "my-wallet",
        title:"Streams",
        icon:<MusicPlayer />
    },
    {   
        id: "transaction",
        title:"Tour",
        icon:<MusicNoteBeamed />
    },
    {   
        id: "crypto",
        title:"Merch",
        icon:<Bag />
    },
    {   
        id: "exchange",
        title:"Endorsements",
        icon:<HandThumbsUp />
    },
    {   
        id: "settings",
        title:"Settings",
        icon:<Gear />
    },
]


export {links, SidebarLinks};
