import React from 'react';
import { ThreeDotsVertical, MusicPlayer, MusicNoteBeamed, Bag} from 'react-bootstrap-icons';
import "./Currency.css";


const Currency = ({name, value, icon, percentChange}) => {
    return (
            <div className="Currency">
                <div className='Currency-header'>
                    <div>
                        {icon}
                    </div>
                    <div className='Currency-name'>{name}</div>
                    <ThreeDotsVertical className="Currency-menu"/>
                </div>
                <div className="Currency-value">${value}</div>
                <small><span>{percentChange}% </span>This week</small>
            </div>
    )
}

const currencyArray = [
    {
        name: "Streams",
        icon: <MusicPlayer className="Currency-icon"/>,
        value: 8500,
        percentChange: 45,
    },
    {
        name: "Tour",
        icon: <MusicNoteBeamed className="Currency-icon"/>,
        value: 2200,
        percentChange: 35,  
    },
    {
        name: "Merch",
        icon: <Bag className="Currency-icon"/>,
        value: 6000,
        percentChange: 21,  
    },
]

export {Currency, currencyArray};